.widgets {
    flex: 0.3;
}

.widgets__input {
    display: flex;
    align-items: center;
    background-color: var(--x-background);
    padding: 10px;
    border-radius: 20px;
    margin-top: 10px;
    margin-left: 20px;
}

.widgets__input input {
    border: none;
    background-color: var(--x-background);
    outline: none;
}

.widgets__serchIcon {
    color: grey;
}

.widgets__container {
    margin-top: 15px;
    margin-left: 20px;
    padding: 20px;
    padding-top: 5px;
    background-color: #f5f8fa;
    border-radius: 20px;
}

.widgets__container h2 {
    font-size: 18px;
    font-weight: 800;
}